import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { 
  Workflow, 
  WorkflowStatus, 
  TriggerType,
  WorkflowTrigger,
  WorkflowAction,
  CreateWorkflowRequest,
  UpdateWorkflowRequest
} from '@/lib/accelerator/types'

// ==========================================
// GET /api/v1/workflows - List all workflows or get single workflow
// ==========================================
export async function GET(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const subAccountId = searchParams.get('subAccountId')
    const workflowId = searchParams.get('id')
    const folderId = searchParams.get('folderId')
    const status = searchParams.get('status') as WorkflowStatus | null
    const triggerType = searchParams.get('triggerType') as TriggerType | null
    const search = searchParams.get('search')
    const page = parseInt(searchParams.get('page') || '1')
    const limit = Math.min(parseInt(searchParams.get('limit') || '20'), 100)

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // If specific workflow requested
    if (workflowId) {
      const { data: workflow, error } = await supabase
        .from('workflows')
        .select(`
          *,
          workflow_actions (*),
          workflow_folders (id, name, color)
        `)
        .eq('id', workflowId)
        .eq('sub_account_id', subAccountId)
        .single()

      if (error || !workflow) {
        return NextResponse.json(
          { success: false, error: 'Workflow not found' },
          { status: 404 }
        )
      }

      // Transform to API response format
      const transformedWorkflow = transformWorkflow(workflow)

      return NextResponse.json({
        success: true,
        data: transformedWorkflow
      })
    }

    // List all workflows with filtering
    let query = supabase
      .from('workflows')
      .select(`
        *,
        workflow_folders (id, name, color)
      `, { count: 'exact' })
      .eq('sub_account_id', subAccountId)
      .order('created_at', { ascending: false })

    if (folderId) {
      query = query.eq('folder_id', folderId)
    }

    if (status) {
      query = query.eq('status', status)
    } else {
      // Default: exclude archived
      query = query.neq('status', 'archived')
    }

    if (triggerType) {
      query = query.eq('trigger_type', triggerType)
    }

    if (search) {
      query = query.ilike('name', `%${search}%`)
    }

    // Pagination
    const from = (page - 1) * limit
    const to = from + limit - 1
    query = query.range(from, to)

    const { data: workflows, error, count } = await query

    if (error) {
      console.error('Error fetching workflows:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch workflows' },
        { status: 500 }
      )
    }

    const transformedWorkflows = (workflows || []).map(w => transformWorkflow(w, false))

    return NextResponse.json({
      success: true,
      data: transformedWorkflows,
      pagination: {
        page,
        limit,
        total: count || 0,
        totalPages: Math.ceil((count || 0) / limit)
      }
    })

  } catch (error) {
    console.error('Error in GET /api/v1/workflows:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/workflows - Create new workflow
// ==========================================
export async function POST(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      subAccountId,
      name,
      description,
      trigger,
      actions,
      status = 'draft',
      folderId,
      allowMultipleExecutions = true,
      maxExecutionsPerContact,
      createdBy
    } = body as CreateWorkflowRequest & { 
      subAccountId: string
      folderId?: string
      createdBy?: string 
    }

    // Validation
    if (!subAccountId || !name || !trigger?.type) {
      return NextResponse.json(
        { success: false, error: 'subAccountId, name, and trigger.type are required' },
        { status: 400 }
      )
    }

    // Create workflow
    const workflowId = uuidv4()
    const { data: workflow, error: workflowError } = await supabase
      .from('workflows')
      .insert({
        id: workflowId,
        sub_account_id: subAccountId,
        folder_id: folderId,
        name,
        description,
        status,
        trigger_type: trigger.type,
        trigger_config: trigger.config || {},
        trigger_filters: trigger.filters || [],
        allow_multiple_executions: allowMultipleExecutions,
        max_executions_per_contact: maxExecutionsPerContact,
        version: 1,
        created_by: createdBy
      })
      .select()
      .single()

    if (workflowError) {
      console.error('Error creating workflow:', workflowError)
      return NextResponse.json(
        { success: false, error: 'Failed to create workflow' },
        { status: 500 }
      )
    }

    // Create actions if provided
    if (actions && actions.length > 0) {
      const actionsToInsert = actions.map((action, index) => ({
        id: uuidv4(),
        workflow_id: workflowId,
        action_type: action.type,
        name: action.name || `Action ${index + 1}`,
        config: action.config || {},
        delay_minutes: action.delay || 0,
        continue_on_error: action.continueOnError || false,
        execution_order: action.order ?? index,
        conditions: action.conditions || []
      }))

      const { error: actionsError } = await supabase
        .from('workflow_actions')
        .insert(actionsToInsert)

      if (actionsError) {
        console.error('Error creating workflow actions:', actionsError)
        // Don't fail the whole request, workflow is created
      }
    }

    // Fetch the complete workflow with actions
    const { data: completeWorkflow } = await supabase
      .from('workflows')
      .select(`
        *,
        workflow_actions (*),
        workflow_folders (id, name, color)
      `)
      .eq('id', workflowId)
      .single()

    return NextResponse.json({
      success: true,
      data: transformWorkflow(completeWorkflow)
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/workflows:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/workflows - Update workflow
// ==========================================
export async function PUT(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      id,
      subAccountId,
      name,
      description,
      status,
      folderId,
      trigger,
      actions,
      allowMultipleExecutions,
      maxExecutionsPerContact
    } = body as UpdateWorkflowRequest & { 
      id: string
      subAccountId: string
      folderId?: string 
    }

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    // Get current workflow to check ownership and increment version
    const { data: currentWorkflow } = await supabase
      .from('workflows')
      .select('version')
      .eq('id', id)
      .eq('sub_account_id', subAccountId)
      .single()

    if (!currentWorkflow) {
      return NextResponse.json(
        { success: false, error: 'Workflow not found' },
        { status: 404 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {
      updated_at: new Date().toISOString(),
      version: currentWorkflow.version + 1
    }

    if (name !== undefined) updates.name = name
    if (description !== undefined) updates.description = description
    if (status !== undefined) updates.status = status
    if (folderId !== undefined) updates.folder_id = folderId
    if (trigger !== undefined) {
      updates.trigger_type = trigger.type
      updates.trigger_config = trigger.config || {}
      updates.trigger_filters = trigger.filters || []
    }
    if (allowMultipleExecutions !== undefined) updates.allow_multiple_executions = allowMultipleExecutions
    if (maxExecutionsPerContact !== undefined) updates.max_executions_per_contact = maxExecutionsPerContact

    const { data: workflow, error } = await supabase
      .from('workflows')
      .update(updates)
      .eq('id', id)
      .eq('sub_account_id', subAccountId)
      .select()
      .single()

    if (error || !workflow) {
      console.error('Error updating workflow:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update workflow' },
        { status: 500 }
      )
    }

    // Update actions if provided
    if (actions !== undefined) {
      // Delete existing actions
      const { error: deleteError } = await supabase
        .from('workflow_actions')
        .delete()
        .eq('workflow_id', id)
      
      if (deleteError) {
        console.error('Error deleting actions:', deleteError)
      }

      // Insert new actions
      if (actions.length > 0) {
        const actionsToInsert = actions.map((action, index) => ({
          id: action.id || uuidv4(),
          workflow_id: id,
          action_type: action.type,
          name: action.name || `Action ${index + 1}`,
          config: action.config || {},
          delay_minutes: action.delay || 0,
          continue_on_error: action.continueOnError !== false, // Default to true
          execution_order: action.order ?? index,
          conditions: action.conditions || []
        }))

        const { error: insertError } = await supabase
          .from('workflow_actions')
          .insert(actionsToInsert)
        
        if (insertError) {
          console.error('Error inserting actions:', insertError)
          return NextResponse.json(
            { success: false, error: `Failed to save actions: ${insertError.message}` },
            { status: 500 }
          )
        }
      }
    }

    // Fetch the complete workflow with actions
    const { data: completeWorkflow } = await supabase
      .from('workflows')
      .select(`
        *,
        workflow_actions (*),
        workflow_folders (id, name, color)
      `)
      .eq('id', id)
      .single()

    return NextResponse.json({
      success: true,
      data: transformWorkflow(completeWorkflow)
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/workflows:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/workflows - Archive or delete workflow
// ==========================================
export async function DELETE(request: NextRequest) {
  try {
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const id = searchParams.get('id')
    const subAccountId = searchParams.get('subAccountId')
    const permanent = searchParams.get('permanent') === 'true'

    if (!id || !subAccountId) {
      return NextResponse.json(
        { success: false, error: 'id and subAccountId are required' },
        { status: 400 }
      )
    }

    if (permanent) {
      // Permanent delete
      const { error } = await supabase
        .from('workflows')
        .delete()
        .eq('id', id)
        .eq('sub_account_id', subAccountId)

      if (error) {
        console.error('Error deleting workflow:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to delete workflow' },
          { status: 500 }
        )
      }
    } else {
      // Soft delete (archive)
      const { error } = await supabase
        .from('workflows')
        .update({ 
          status: 'archived',
          updated_at: new Date().toISOString()
        })
        .eq('id', id)
        .eq('sub_account_id', subAccountId)

      if (error) {
        console.error('Error archiving workflow:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to archive workflow' },
          { status: 500 }
        )
      }
    }

    return NextResponse.json({ success: true }, { status: 200 })

  } catch (error) {
    console.error('Error in DELETE /api/v1/workflows:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformWorkflow(dbWorkflow: any, includeActions: boolean = true): Workflow {
  const workflow: Workflow = {
    id: dbWorkflow.id,
    subAccountId: dbWorkflow.sub_account_id,
    name: dbWorkflow.name,
    description: dbWorkflow.description,
    status: dbWorkflow.status,
    
    trigger: {
      type: dbWorkflow.trigger_type,
      config: dbWorkflow.trigger_config || {},
      filters: dbWorkflow.trigger_filters || []
    },
    
    actions: [],
    
    allowMultipleExecutions: dbWorkflow.allow_multiple_executions,
    maxExecutionsPerContact: dbWorkflow.max_executions_per_contact,
    executionDelay: dbWorkflow.execution_delay_minutes,
    
    totalExecutions: dbWorkflow.total_executions,
    successfulExecutions: dbWorkflow.successful_executions,
    failedExecutions: dbWorkflow.failed_executions,
    lastExecutedAt: dbWorkflow.last_executed_at,
    
    createdAt: dbWorkflow.created_at,
    updatedAt: dbWorkflow.updated_at,
    createdBy: dbWorkflow.created_by,
    version: dbWorkflow.version
  }

  // Transform actions if included
  if (includeActions && dbWorkflow.workflow_actions) {
    workflow.actions = dbWorkflow.workflow_actions
      .sort((a: any, b: any) => a.execution_order - b.execution_order)
      .map((action: any) => ({
        id: action.id,
        type: action.action_type,
        name: action.name,
        config: action.config || {},
        delay: action.delay_minutes,
        continueOnError: action.continue_on_error,
        order: action.execution_order,
        conditions: action.conditions || []
      }))
  }

  // Add folder info if available
  if (dbWorkflow.workflow_folders) {
    (workflow as any).folder = {
      id: dbWorkflow.workflow_folders.id,
      name: dbWorkflow.workflow_folders.name,
      color: dbWorkflow.workflow_folders.color
    }
  }

  return workflow
}
